// vue,thisvue,
// store,;
import store from '@/store/index.js';

import http from './http.js'

import {formatDate} from '../utils.js'

import {url_putFeedback} from './http_req_list.js'

// login,code,token,token
// , ID

export const http_putFeedback = function(msgContent, msgTitle, msgTitleName) {
	

  // "createTime": "",
  // "dataSort": "",
  // "id": "ID",
  // "isDelete": "",
  // "msgContent": "",
  // "msgMobile": 15132582599,
  // "msgResult": "",
  // "msgTime": ", ",
  // "msgTitle": "",
  // "msgTitleName": "string",
  // "nickName": "string",
  // "status": "",
  // "updateTime": "",
  // "userId": "ID",
  
	let userInfo = store.getters.store_UserInfoData
	return new Promise(async (resolve, reject) => {
		await http.post(url_putFeedback, {
			msgContent, 
			msgTitle,
			msgTitleName,
			'msgTime': formatDate(new Date(new Date().getTime())),
			'msgMobile': userInfo.mobile, 
			'nickName':userInfo.nickName,
			'id': userInfo.id
		}).then(res => {
		
			console.log(res)
			if (res.data.code === 0) {
				resolve(true)
			}  else {
				
				resolve(false)
			}
		
		}).catch(error => {
			console.log(error)
			resolve(false)
		}).finally(() => {})
	})
	
}
